.about {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 100px;
}

.about__title {
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
  text-align: center;
  margin-bottom: 40px;
}

.about__text {
  text-align: center;
  color: #787878;
  font-family: "Nunito Sans";
  font-size: 16px;
  line-height: 24px;
}

.about__signature {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
